body {
    font: 14px "Century Gothic", Futura, sans-serif;
    margin: 20px;
}


ul {
    padding: 0;
    margin: 0;
    list-style: none;
    background: #f2f2f2;
    max-width: 100px;
}
ul li {
    display: inline-block;
}
ul li a {
    display: block;
    padding: 10px 25px;
    background: #ffffff;
    text-decoration: solid;
    text-align: left;
    border-top: 1px solid #ffffff;
    white-space: nowrap;
}
ul li a:hover {
    color: #fff;
    background: #939393;
}

.menu {
    float: right;
    position: relative;
}

.mainImage {
    width: 600px;
    height: 400px;
    top:70%;
    left:50%;
    position: fixed;
    transform: translate(-50%, -50%);
}

.mainTitle {
    font: 50px "Calibre", Futura, san-serif;
    margin: 30px;
    block-size: auto;
}

.subTitle {
    font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
    font-size: xx-large;
    margin: 30px;
}

.titleCentered {
    position: fixed;
    top: 20%;
    left: 50%;
    transform: translate(-50%, -50%);
}